import Vue from 'vue'
import VueRouter from 'vue-router'
import eventiApp from '../views/eventi-app.cmp.vue'
import eventiEdit from '../views/eventi-edit.cmp.vue'
import eventiDetails from '../views/eventi-details.cmp.vue'
import dashboardApp from '../views/dashboard-app.cmp.vue'
import aboutApp from '../views/about-app.cmp.vue'
import userDetails from '../views/user-details.cmp.vue'
import aboutUser from '../views/About.vue'
import login from '../views/login.vue'
import review from '../views/review.cmp.vue'


Vue.use(VueRouter)


const routes = [{
        path: '/eventi',
        component: eventiApp
    },
    {
        path: '/dashboard',
        component: dashboardApp
    },
    {
        path: '/about',
        component: aboutApp
    },
    {
        path: '/eventi/edit/:eventiId?',
        component: eventiEdit
    },
    {
        path: '/eventi/:eventiId',
        component: eventiDetails
    },
    {
        path: '/user/about',
        component: aboutUser
    },
    {
        path: '/user/login',
        component: login
    },
    {
        path: '/user/:id',
        component: userDetails
    },
    {
        path: '/review',
        component: review
    }
];


const router = new VueRouter({
    routes
})

export default router