<script>
// CommitChart.js
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  mounted() {
    // Overwriting base render method with actual data.
    this.renderChart({
      labels: [this.types[0], this.types[1], this.types[2]],
      datasets: [
        {
          label: "Avg Prices",
          backgroundColor: "#f87979",
          data: [this.avgPrices[0], this.avgPrices[1], this.avgPrices[2]],
        },
      ],
    });
  },
  props: ["types", "avgPrices"],
  created() {
    console.log(this.avgPrices);
  },
};

