<template>
  <section v-if="eventi" class="eventi-details flex column center">
      <button @click="$router.push('/eventi')">Back</button>
      <h1>Eventi Details</h1>
      <span>Name: {{ eventi.name }}</span>
      <span>Stock: {{ eventi.inStock }}</span>
      <span>Type: {{ eventi.type }}</span>
      <span>Price: ${{eventi.price}}</span>
      <span>Created At: {{ timeToPresent }}</span>
      <span>Updated At: {{ updateTime }}</span>
      <section class="imgs flex center">
      <img v-for="(imgUrl, idx) in eventi.imgUrls" :src="imgUrl" :key="idx" alt="img...">
      </section>
      <h3>Reviews:</h3>
      <section class="reviews flex">
        <section class="review" v-for="review in reviews" :key="review._id">
          <h3>{{ review.txt }}</h3>
          <p>
            By
            <router-link :to="`/user/${review.byUser._id}`">
              {{ review.byUser.username }}
            </router-link>
          </p>
        </section>
      </section>
      <form class="add-review flex center" @submit.prevent="addReview()">
        <hr />
        <textarea
          placeholder="Your Opinion Matters..."
          v-model="reviewToEdit.txt"
        ></textarea>
        <button>Save Review</button>
      </form>

  </section>
</template>

<script>
import { eventiService } from '../services/eventi.service.js'
export default {
  name: 'eventi-details',
  data() {
    return {
      eventi: null,
      timeToPresent: '',
      updateTime: '--',
      reviewToEdit: { txt: '', aboutEventi: null }
    }
  },
  methods: {
    addReview() {
      this.reviewToEdit.aboutEventi = this.eventi
      this.$store.dispatch({ type: 'addReview', review: this.reviewToEdit })
      this.reviewToEdit = { txt: '', aboutEventi: null }
    }
  },
  computed: {
    reviews() {
      var filterReviews = []
      const reviews = this.$store.getters.reviews;
      reviews.forEach(review => {
        console.log(review.aboutEventi._id);
        if (review.aboutEventi._id === this.eventi._id) {
          filterReviews.push(review)
        }
      })
      return filterReviews
    },

  },
  async created() {
    const id = this.$route.params.eventiId
    const eventi = await eventiService.getById(id)
    this.eventi = JSON.parse(JSON.stringify(eventi))
    this.timeToPresent = `${new Date(this.eventi.createdAt).getDate()}.${new Date(this.eventi.createdAt).getMonth() + 1}.${new Date(this.eventi.createdAt).getFullYear()} ${new Date(this.eventi.createdAt).toLocaleTimeString()}`
    if (eventi.updateAt) {
      this.updateTime = `${new Date(this.eventi.updateAt).getDate()}.${new Date(this.eventi.updateAt).getMonth() + 1}.${new Date(this.eventi.updateAt).getFullYear()} ${new Date(this.eventi.updateAt).toLocaleTimeString()}`
    }
  }

}
</script>
