<template>
  <section class="eventi-list flex column center">
    <button @click="$router.push('/eventi/edit')" class="add-btn">Add</button>
    <section class="main-content flex">
      <section class="eventi flex column center" v-for="eventi in eventis" :key="eventi._id">
        <eventi-preview :eventi="eventi" />
        <section class="btns">
          <button @click.stop="onRemove(eventi)">X</button>
          <button @click.stop="toEdit(eventi)">Update</button>
          <button @click.stop="toDetails(eventi)">Details</button>
        </section>
      </section>
    </section>
  </section>
</template>

<script>
import eventiPreview from "./eventi-preview.cmp.vue";
export default {
  name: "eventi-list",
  props: {
    eventis: Array,
  },
  methods: {
    onRemove(eventi) {
      this.$emit("removeEventi", eventi._id);
    },
    toEdit(eventi) {
      this.$emit("toEdit", eventi._id);
    },
    toDetails(eventi) {
      this.$router.push(`/eventi/${eventi._id}`);
    },
  },
  components: {
    eventiPreview,
  },
};
</script>
