<template>
  <section class="reviews flex column center wrap text-center">
    <form class="add-review flex center" @submit.prevent="addReview()">
      <select v-model="reviewToEdit.aboutEventi">
        <option value="" selected disabled hidden>Choose Eventi</option>
        <option v-for="eventi in eventis" :key="eventi._id" :value="eventi">
          {{ eventi.name }}
        </option>
      </select>
      <textarea
        placeholder="Your Opinion Matters..."
        v-model="reviewToEdit.txt"
      ></textarea>
      <button>Save Review</button>
      <hr />
    </form>
    <section class="reviews flex center wrap">
      <section class="review" v-for="review in reviews" :key="review._id">
        <h3>{{ review.txt }}</h3>
        <p>
          About
          <router-link :to="`eventi/${review.aboutEventi._id}`">
            {{ review.aboutEventi.name }}
          </router-link>
        </p>
        <p>
          By
          <router-link :to="`user/${review.byUser._id}`">
            {{ review.byUser.username }}
          </router-link>
        </p>
        <button @click="removeReview(review._id)">X</button>
      </section>
    </section>
  </section>
</template>

<script>
export default {
  data() {
    return {
      reviewToEdit: { txt: '', aboutEventi: null }
    }
  },
  computed: {
    reviews() {
      return this.$store.getters.reviews;
    },
    eventis() {
      return this.$store.getters.eventisForDisplay
    }
  },
  methods: {
    removeReview(reviewId) {
      this.$store.dispatch({ type: 'removeReview', reviewId })
    },
    addReview() {
      this.$store.dispatch({ type: 'addReview', review: this.reviewToEdit })
      this.reviewToEdit = { txt: '', aboutEventi: null }
    }
  }
}
</script>

