function save(eventi) {
    const savedEventi = (eventi._id) ? _update(eventi) : _add(eventi)
    return savedEventi
}

function _add(eventi) {
    eventi.createdAt = Date.now()
    console.log(eventi);
    // return axios.post(BASE_URL, eventi)
    //     .then(res => res.data)
    //     .then((eventi) => {
    eventBusService.$emit(SHOW_MSG, { txt: `${eventi.name} Added Succefully`, type: 'success' });
    return HttpService.post(`eventi`, eventi)

    //     return eventi
    // })
}

function _update(eventi) {
    // console.log('check', eventi);
    eventi.updateAt = Date.now()
        //     // return axios.put(`${BASE_URL}/${eventi._id}`, eventi)
        //     //     .then(res => res.data)
        //     //     .then((eventi) => {
        // eventBusService.$emit(SHOW_MSG, { txt: `${eventi._id} Updated Succefully`, type: 'success' });
        // console.log( HttpService.put(`eventi/${eventi._id}`, eventi));
    return HttpService.put(`eventi/${eventi._id}`, eventi)
        //     return eventi
        // })
}

function getEmptyEventi() {
    return {
        name: '',
        price: null,
        type: '',
        inStock: true,
    }
}