<template>
  <section class="eventi-edit flex column center">
    <button @click="$router.push('/eventi')">Back</button>
    <h2>Eventi Edit</h2>
    <form @submit.prevent="addEventi" class="flex column center text-center">
      <label>
        <span>Eventi Name:</span>
        <input type="text" placeholder="Enter Eventi" v-model="eventiToEdit.name" />
      </label>
      <label>
        <span>Eventi Price:</span>
        <input
          type="number"
          placeholder="Enter Price"
          v-model="eventiToEdit.price"
        />
      </label>
      <label>
        <span>Eventi Type:</span>
        <select v-model="eventiToEdit.type">
          <option value="Adult">Adult</option>
          <option value="Funny">Funny</option>
          <option value="Educational">Educational</option>
        </select>
      </label>
      <label for="checkbox">
        inStock
        <input type="checkbox" id="checkbox" v-model="eventiToEdit.inStock" />
      </label>
      <label class="img-list">
        Pictures:
      <section class="imgs flex center wrap">
        <section class="img-edit flex column center" v-for="(imgUrl, idx) in eventiToEdit.imgUrls" :key="idx">
          <img :src="imgUrl" alt="img...">
          <button class="remove-img" @click.stop.prevent="removeImg(idx)">X</button>
        </section>
      </section>
      </label>

        
      <section class="upload-img">
        <template v-if="!isLoading">
          <label for="imgUploader"> <img class="img-uploader" src="https://lh3.googleusercontent.com/proxy/ybV5upOkFlWYVsEN_aWw4UmnHgjOlEG9kzWbDfedBxVYU_jO6UK1bNgdzCGO1FRTT0oCoDPbtweZrB-Qc-dIopuMGygTvvhWdoJznm-RN6g9O_wF-5EuVg4Rr1o" alt=""> </label>
          <input type="file" name="img-uploader" id="imgUploader" @change="onUploadImg">  
        </template>
        <img class="loader" v-else src="https://i.pinimg.com/originals/65/ba/48/65ba488626025cff82f091336fbf94bb.gif" alt="">
        <div class="img-list flex center">
          <img v-for="(imgUrl, idx) in imgUrls" :src="imgUrl" :key="idx" alt="img...">
        </div>
      </section>
      <button class="save-btn">Save</button>
    </form>
  </section>
</template>

<script>
import { eventiService } from "../services/eventi.service.js";
import { uploadImg } from '../services/img-upload.service.js'

export default {
  name: "eventi-edit",
  data() {
    return {
      eventiToEdit: eventiService.getEmptyEventi(),
      isLoading: false,
      imgUrls: []

    };
  },
  methods: {
    async onUploadImg(ev) {
      this.isLoading = true;
      const res = await uploadImg(ev);
      this.imgUrls.push(res.url)
      this.isLoading = false;
    },
    addEventi() {
      if(!this.eventiToEdit.imgUrls) this.eventiToEdit.imgUrls = []
      this.eventiToEdit.imgUrls.push(...this.imgUrls);
      this.$store.dispatch({
        type: 'saveEventi',
        eventi: JSON.parse(JSON.stringify(this.eventiToEdit)),
      });
      this.eventiToEdit = eventiService.getEmptyEventi();
      this.$router.push('/eventi');
    },
    removeImg(idx) {
      console.log(idx);
      this.eventiToEdit.imgUrls.splice(idx, 1)
    }
  },
  async created() {
    const id = this.$route.params.eventiId;
    console.log(id);
    if (id) {
      const eventi = await eventiService.getById(id)
      this.eventiToEdit = JSON.parse(JSON.stringify(eventi));
    }
    console.log('this.eventiToEdit', this.eventiToEdit);
  },
};
</script>

<style>
.loader {
  height: 150px;
}

.img-list img {
  height: 100px;
}

.img-uploader{
  height: 50px;
}
.img-uploader:hover {
  cursor: pointer;
}

input[name="img-uploader"] {
  opacity: 0;
}

</style>