<template>
  <section class="eventi-filter flex column center">
    <h3>Filter Eventis</h3>
    <form @click.prevent="emitFilter">
      <input
        type="text"
        v-model="filterBy.byName"
        placeholder="Search by name"
        @input="emitFilter"
      />
      <select v-model="filterBy.status">
        <option value="All">All</option>
        <option value="Stock">Stock</option>
        <option value="noStock">No Stock</option>
      </select>
      <!-- <my-select-filter-status v-model="filterBy.status" /> -->
      <select v-model="filterBy.type">
        <option value="" selected disabled hidden>Filter By Type</option>
        <option value="All">All</option>
        <option value="Adult">Adult</option>
        <option value="Funny">Funny</option>
        <option value="Educational">Educational</option>
      </select>
      <!-- <my-select-filter-type v-model="filterBy.type" /> -->
      <select v-model="sortBy">
        <option value="" selected disabled hidden>Sort By</option>
        <option value="name">Name</option>
        <option value="createdAt">Date created</option>
        <option value="updateAt">Date updated</option>
        <option value="price">Price</option>
      </select>
      <!-- <my-select-sort v-model="sortBy" /> -->
    </form>
    <!-- <my-select :options="typeOptions" v-model="filterBy.type" /> -->
  </section>
</template>

<script>
import mySelectSort from "../my-select-sort.vue";
import mySelectFilterType from "../my-select-filter-type.vue";
import mySelectFilterStatus from "../my-select-filter-status.vue";
import mySelect from "../my-select.vue";

export default {
  name: "eventi-filter",
  data() {
    return {
      filterBy: { byName: "", status: "All", type: "All" },
      sortBy: "",
      debounce: null,
      typeOptions: [
        {
          value: "All",
          label: "All",
        },
        {
          value: "Funny",
          label: "Funny",
        },
        {
          value: "Educational",
          label: "Educational",
        },
        {
          value: "Adult",
          label: "Adult",
        },
      ],
    };
  },
  methods: {
    emitFilter() {
      if (this.debounce) clearTimeout(this.debounce);
      this.debounce = setTimeout(() => {
        this.$emit("doFilter", JSON.parse(JSON.stringify(this.filterBy)));
        this.$emit("doSort", JSON.parse(JSON.stringify(this.sortBy)));
      }, 1000);
    },
  },
  components: {
    mySelectSort,
    mySelectFilterType,
    mySelectFilterStatus,
    mySelect,
  },
};
</script>

<style lang="scss" scoped>
.eventi-filter form > * {
  margin: 5px;
}
</style>
