<template>
  <div class="login-logout">
    <img src="../assets/img/mr-eventi-logo.png" />
    <h2>{{ msg }}</h2>

    <div v-if="loggedinUser">
      <h3>
        Loggedin User:
        {{ loggedinUser.username }}
        <form @submit.prevent="updateUser">
          <input type="text" v-model="userToEdit.username" />
        </form>
        <button @click="doLogout">Logout</button>
      </h3>
    </div>
    <div v-else>
      <form @submit.prevent="doLogin">
        <input type="text" v-model="loginCred.email" placeholder="Email" />
        <br />
        <input
          type="text"
          v-model="loginCred.password"
          placeholder="Password"
        />
        <br />
        <button>Login</button>
      </form>

      <form @submit.prevent="doSignup">
        <input type="text" v-model="signupCred.email" placeholder="Email" />
        <br />
        <input
          type="text"
          v-model="signupCred.password"
          placeholder="Password"
        />
        <br />
        <input
          type="text"
          v-model="signupCred.username"
          placeholder="Username"
        />
        <br />
        <button>Signup</button>
      </form>
    </div>
    <section class="edit-user" v-if="loggedinUser && loggedinUser.isAdmin">
      <hr />
      <button @click="getAllUsers">Get All Users</button>
      <section class="users flex center column">
        <section
          class="user flex center text-center"
          v-for="user in users"
          :key="user._id"
        >
          <span>{{ user.username }}</span>
          <button @click="removeUser(user._id)">x</button>
        </section>
      </section>
    </section>
  </div>
</template>

<script>
export default {
  name: 'login',
  data() {
    return {
      loginCred: {},
      signupCred: {},
      msg: '',
      userToEdit: {}
    }
  },
  computed: {
    users() {
      return this.$store.getters.users
    },
    loggedinUser() {
      return this.$store.getters.loggedinUser

    }
  },
  created() {
    console.log('check');
    console.log('this.loggedinUser', this.loggedinUser)
  },
  methods: {
    async doLogin() {
      const cred = this.loginCred
      if (!cred.email || !cred.password) return this.msg = 'Please enter user/password'
      try {
        await this.$store.dispatch({ type: 'login', userCred: cred })
        this.loginCred = {};
      } catch (err) {
        this.msg = 'Your user/password wrong, please try again.'
      }

    },
    doLogout() {
      this.$store.dispatch({ type: 'logout' })
    },
    doSignup() {
      const cred = this.signupCred
      if (!cred.email || !cred.password || !cred.username) return this.msg = 'Please fill up the form'
      this.$store.dispatch({ type: 'signup', userCred: cred })
      this.signupCred = {}
    },
    getAllUsers() {
      this.$store.dispatch({ type: 'loadUsers' })
    },
    removeUser(userId) {
      this.$store.dispatch({ type: 'removeUser', userId })
    },
    updateUser() {
      this.$store.dispatch({ type: 'updateUser', user: this.userToEdit })
    },
  },
  watch: {
    loggedinUser() {
      this.userToEdit = JSON.parse(JSON.stringify(this.loggedinUser))
    }
  }
}
</script>