<template>
    <el-select v-model="value" placeholder="Select" @input="emitChange">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
</template>

<script>
export default {
  props: {
    options: Array,
  },
  data() {
    return {
      value: "",
    };
  },
  methods: {
    emitChange() {
      this.$emit("input", this.value);
    },
  },
  created() {
    console.log(this.options);
  },
};
</script>