<template>
  <el-select v-model="value" @change="statusSelect" placeholder="Select">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      options: [
        {
          value: "All",
          label: "All",
        },
        {
          value: "Stock",
          label: "Stock",
        },
        {
          value: "noStock",
          label: "No Stock",
        },
      ],
      value: "Filter by stock",
    };
  },
};
</script>
