<template>
    <section class="user-details flex column center" v-if="user">
        <h1>User Details - {{user.username}}</h1>
        <h3>email: {{user.email}}</h3>
        <h3>given reviews:</h3>
        <section class="reviews flex center">
            <section class="review flex column center" v-for="review in reviews" :key="review._id">
                <span>
                About
                <router-link :to="`/eventi/${review.aboutEventi._id}`">
                    {{ review.aboutEventi.name }}
                </router-link>
                </span>
                <span>"{{review.txt}}"</span>
            </section>
        </section>
    </section>
</template>

<script>
import UserService from '../services/UserService';

export default {
    name: 'user-details',
    data() {
        return {
            user: null,
        }
    },
    computed: {
        reviews() {
            var reviews =[]
            this.$store.getters.reviews.forEach(review => {
                if(review.byUser._id === this.user._id) {
                    reviews.push(review)
                }
            })
            return reviews
        }
    },
    async created() {
        const id = this.$route.params.id;
        const user = await UserService.getById(id);
        this.user = user
    }
}
</script>