<template>
  <div id="app" class="app">
    <header>
      <header-app />
    </header>
    <main>
      <router-view />
    </main>
    <footer class="full">
      <footer-app />
    </footer>
  </div>
</template>

<script>
import headerApp from "./cmps/header-app.cmp";
import footerApp from "./cmps/footer-app.cmp";

export default {
  created() {
    console.log("Vue App was created!!!");
    this.$store.dispatch({
      type: "setEventis",
    });
    const user =  JSON.parse(sessionStorage.getItem('user'))
    this.$store.dispatch({
      type: "setUser",
      user
    });
    this.$store.dispatch({type: 'loadUsers'})
    this.$store.dispatch({type: 'loadReviews'})
  },
  components: {
    headerApp,
    footerApp,
  },
};
</script>

