

<template>
  <section class="eventi-app center column">
    <section class="header-content flex center column">
      <img class="logo" src="../assets/img/mr-eventi-logo.png" />
      <h2>Your master when it comes to eventis</h2>
    </section>
    <user-msg />
    <button @click="openChat" class="chat-btn"></button>
    <chat-app v-if="showChat" class="chat-app" @closeChat="closeChat">
      <button slot="header" @click="closeChat">X</button>
      <section v-for="(msg, idx) in msgs" class="main" :key="idx">
        <span class="msg">{{ msg }}</span>
      </section>
      <div slot="footer">
        <form @submit.prevent="sendMsg">
          <input type="text" placeholder="Send Massage" v-model="msgToSend" />
          <button>send</button>
        </form>
      </div>
    </chat-app>
    <eventi-filter @doFilter="setFilter" @doSort="setSort" />
    <eventi-list @removeEventi="removeEventi" @toEdit="toEdit" :eventis="eventis" />
  </section>
</template>

<script>
import eventiList from '../cmps/eventi/eventi-list.cmp';
import eventiFilter from '../cmps/eventi/eventi-filter.cmp.vue';
import userMsg from '../cmps/user-msg.vue';
import chatApp from '../cmps/chat-app.vue';

export default {
  name: 'eventi-app',
  data() {
    return {
      filterBy: null,
      sortBy: null,
      msgs: [],
      msgToSend: '',
      supportMsg: 'Support: Sure Bro!',
      showChat: false,
      debounce: null,
    };
  },
  computed: {
    eventis() {
      const eventis = this.$store.getters.eventisForDisplay;
      if (!this.filterBy) {
        return eventis;
      }
      var eventisFilter = [];
      const txt = this.filterBy.byName.toLowerCase();
      eventisFilter = eventis.filter((eventi) => eventi.name.toLowerCase().includes(txt));

      // if (this.filterBy.status === "All") {
      //   return eventisFilter;
      // }
      if (this.filterBy.status === "Stock") {
        eventisFilter = eventisFilter.filter((eventi) => eventi.inStock);
      }
      if (this.filterBy.status === "noStock") {
        eventisFilter = eventisFilter.filter((eventi) => !eventi.inStock);
      }
      if (this.filterBy.type === "Funny") {
        return eventisFilter.filter((eventi) => eventi.type === "Funny");
      }
      if (this.filterBy.type === "Adult") {
        return eventisFilter.filter((eventi) => eventi.type === "Adult");
      }
      if (this.filterBy.type === "Educational") {
        return eventisFilter.filter((eventi) => eventi.type === "Educational");
      }
      return eventisFilter;
    },
  },
  methods: {
    setFilter(filterBy) {
      this.filterBy = filterBy;
    },
    setSort(sortBy) {
      this.$store.dispatch({
        type: "setSortEventis",
        sortBy,
      });
    },
    removeEventi(eventiId) {
      const eventi = this.eventis.find((eventi) => eventi._id === eventiId);
      this.$store.dispatch({
        type: "removeEventi",
        eventi,
      });
    },
    toEdit(eventiId) {
      this.$router.push(`/eventi/edit/${eventiId}`);
    },
    sendMsg() {
      const msg = `Ya: ${this.msgToSend}`;
      if (!this.debounce) clearTimeout(this.debounce);
      this.debounce = setTimeout(() => {
        this.msgs.push(msg);
        this.msgs.push(this.supportMsg);
      }, 500);
      this.msgToSend = "";
    },
    closeChat() {
      this.showChat = false;
      this.msgs = [];
      this.msgToSend = "";
    },
    openChat() {
      this.showChat = true;
    },
  },
  components: {
    eventiList,
    eventiFilter,
    userMsg,
    chatApp,
  },
};
</script>
