<template>
  <div class="dashboard-app">
    <chart :types="types" :avgPrices="avgPrices" class="chart" />
  </div>
</template>

<script>
import chart from "../cmps/chart";
export default {
  name: "dashboard-app",
  components: {
    chart,
  },
  data() {
    return {
      types: ["Adult", "Funny", "Educational"],
      avgPrices: [],
    };
  },
  computed: {
    eventis() {
      return this.$store.getters.eventisForDisplay
    }
  },
  methods: {
    getAvgPrice() {
      if(!this.eventis) return
      this.types.forEach((type) => {
        var countType = 0;
        var totalPrice = 0;
        this.eventis.forEach((eventi) => {
          if (eventi.type === type) {
            totalPrice = totalPrice + eventi.price;
            countType++;
          }
        });
        this.avgPrices.push(totalPrice / countType);
      });
    },
  },
  mounted() {
    this.getAvgPrice();
  },
};
</script>

